<!--  -->
<template>
  <div class="cell" v-on:click="clickSelf">
    <template v-if="a">{{text}}</template>
    <template v-else></template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      a: false,
      text: ""
    };
  },

  props: ["n"],

  components: {},

  created() {},

  computed: {},

  mounted() {},

  methods: {
    // 点击棋盘事件
    clickSelf() {
      if (this.text !== "") {
        return; //  如果已经被点击了就不能点击
      }
      this.a = true;
      this.text = this.n % 2 === 0 ? "x" : "o";
      this.$emit("single", this.text);
    }
  }
};
</script>
<style>
.cell {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  border: 1px solid #000;
  font-size: 80px;
  box-sizing: border-box;
}
</style>
