<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->

    <div class="chess">
      <p>第{{n}}手</p>
      <div class="row">
        <cell @single="getNum(0,$event)" :n="n" />
        <cell @single="getNum(1,$event)" :n="n" />
        <cell @single="getNum(2,$event)" :n="n" />
      </div>

      <div class="row">
        <cell @single="getNum(3,$event)" :n="n" />
        <cell @single="getNum(4,$event)" :n="n" />
        <cell @single="getNum(5,$event)" :n="n" />
      </div>

      <div class="row">
        <cell @single="getNum(6,$event)" :n="n" />
        <cell @single="getNum(7,$event)" :n="n" />
        <cell @single="getNum(8,$event)" :n="n" />
      </div>
      <p>map:{{map}}</p>
      <p>结果是:{{ovo === null ?'胜负未分':`胜方是${ovo}`}}</p>
      <!-- <p>finished:{{finished}}</p> -->
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import Cell from "@/components/Cell.vue";

export default {
  name: "home",
  data() {
    return {
      n: 0, // 获得被点击的num，默认为0，没有cell被点击
      map: [[null, null, null], [null, null, null], [null, null, null]], //声明一个二维数组，记录棋盘点击
      ovo: null,
      // finished: false //游戏结束
    };
  },
  components: {
    // HelloWorld
    Cell
  },
  methods: {
    getNum(x, text) {
      // console.log(`${x}被点击，内容是${text}`);
      this.map[Math.floor(x / 3)][x % 3] = text;
      this.n = this.n + 1;
      this.tell(); //调用tell判断输赢 有个问题，为什么第一次点击 result就会变成null？知道有人赢了才有值？
    },
    tell() {
      const map = this.map;
      for (let i = 0; i < 2; i++) {
        // 判断每行
        if (
          map[i][0] !== null &&
          map[i][0] === map[i][1] &&
          map[i][1] === map[i][2]
        ) {
          this.ovo = map[i][0];
        }
      }
      for (let k = 0; k < 2; k++) {
        // 判断每列this.ovo = null
        if (
          map[0][k] !== null &&
          map[0][k] === map[1][k] &&
          map[1][k] === map[2][k]
        ) {
          this.ovo = map[0][k];
        }
      }
      //判断\
      if (
        map[(0)[0]] !== null &&
        map[0][0] === map[1][1] &&
        map[1][1] === map[2][2]
      ) {
        this.ovo = map[0][0];
      }

      //判断/
      if (
        map[(0)[2]] !== null &&
        map[0][2] === map[1][1] &&
        map[1][1] === map[2][0]
      ) {
        this.ovo = map[0][2];
      } 
    }
  }
};
</script>
